body {
  background-color: #f5f0f0;
  box-sizing: border-box;
}

.logo {
  height: 4rem;
  width: 4rem;
}

.homeNav {
  background-color: #FFFFFF;
  color: black;
  box-shadow: 0 4px 2px -2px rgb(206, 201, 201);
  position: fixed;
  top: 0;
  width: 100%;
}
a {
  text-decoration: none;
}

.instagramTitle {
  font-weight: 600;
  font-size: 1.6rem;
  color: black;

}

.homeNavContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;

}
.homeNavContainer > input {
  margin-right: 5rem;
}

.logoAndInstagramTitle {
  display: flex;
  align-items: center;
  padding-right: 10rem;
}

.navBarIcons {
  display: flex;
  justify-content: space-between;
  width: 15rem;
}

.navBarIcons > * {
  height: 1.5rem;
  width: 1.5rem;
}
.navBarIcons > *:hover {
  cursor: pointer;
}
.sidebarContainer {
position: fixed;
right: 25rem;
top: 10rem;
display: flex;
flex-direction: column;
height: 10rem;
justify-content: space-around;
}
.postsContainer {
  border: 1px solid black;
  height: 100vh;
  width: 40rem;
  margin-top: 10rem;
  margin-left: 10rem;
}

.signUpButton, .logInButton {
  border: none;
  color: white;
  background-color: black;
  height: 3rem;
  width: 8rem;
  border-radius: 10px;;
}

.signUpButton:hover, .logInButton:hover {
  cursor: pointer;
  box-shadow: 0 0 15px #44423e;
}
.signUpPage {
  display: flex;
  justify-content: center;
}
.signUpContainer {
  display: grid;
  grid-template-rows: 2.5fr 0.1fr 2.5fr;
  align-items: center;
  justify-items: center;
  height: 100vh;
  width: 20rem;
  border: 1px solid black;
  background-color: #FFFFFF;
}

.alreadySignedUpContainer {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 5rem;
  justify-content: space-between;
}
.addUser {
  display: flex;
  flex-direction: column;
  height: 5rem;
  justify-content: space-between;
}
